.recipe {
  border-radius: 10px;
  box-shadow: 0px 5px 15px rgb(48, 51, 77);
  margin: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background: white;
  align-items: center;
  min-width: 40%;
}

.image {
  border-radius: 50%;
  width: 200px;
  height: 200px;
}

a {
  color: inherit;
  text-decoration: inherit;
}
